  .logo img {
    height: 45px;
    padding: 30px;
  }

  .content {
    display: flex;
  }

  .illustration img {
    padding-top: 30px;
    padding-left: 30px;
    justify-content: start;
  }

  .details {
    padding-top: 35px;
    padding-left: 35px;
  }

  .details h1 {
    color: white;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 2.5rem;
  }

  .details p {
    color: white;
  }

  .details button {
    color: hsl(257, 40%, 49%);
    margin-top: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 55px;
    padding-left: 55px;
    border-radius: 9999px;
    border: none;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }

  .details button:hover {
    color: white;
    background-color: hsl(300, 69%, 71%);
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  }

  .company-logos {
    text-align: end;
    padding-right: 50px;
  }

  .company-logos > #logos {
    border: 2px solid white;
    width: 2rem !important;
    height: 2rem !important;
    border-radius: 9999px;
    margin-right: 10px;
    cursor: pointer;
